.reactions {
  display: flex;
}
.reactions__item {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
  background-color: transparent;
  padding: 0;
  border: none;
}
.reactions__item:not(:last-child) {
  margin-right: 25px;
}
.reactions__icon {
  margin-right: 5px;
  pointer-events: none;
}
.reactions__value {
  font-size: 14px;
  line-height: 20px;
  color: #2c3d52;
  pointer-events: none;
}
@media (max-width: 767px) {
  .reactions__item:not(:last-child) {
    margin-right: 20px;
  }
}